<template>
    <div class="login">
        <div class="login-header">
            <img src="../assets/imgs/logo.png" alt="">
            <h1>海关电子闸监控系统</h1>
        </div>
        <div class="bg">
            <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="70px"
                     class="form">
                <h3 class="title">用户登录</h3>
                <el-form-item prop="account" label="用户名">
                    <el-input type="text" v-model="ruleForm2.account" auto-complete="off"
                              placeholder="admin"></el-input>
                </el-form-item>
                <el-form-item prop="checkPass" label="密码">
                    <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"
                              placeholder="*********"></el-input>
                </el-form-item>
                <el-form-item style="width:100%;" label-width="0">
                    <el-button type="primary" style="width: 100%;" @click.native.prevent="handleSubmit2"
                               :loading="logining">登录
                    </el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="copyright">
            COPYRIGHT@2015-2017   深圳市华瑞自动化系统有限公司
        </div>
    </div>
</template>

<script>
    import { requestLogin } from '../api/api'
    import { mapMutations } from 'vuex'

    export default {
        data () {
            return {
                logining: false,
                ruleForm2: {
                    account: '',
                    checkPass: ''
                },
                rules2: {
                    account: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                        //{ validator: validaePass }
                    ],
                    checkPass: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                        //{ validator: validaePass2 }
                    ]
                },
                checked: true
            }
        },
        methods: {
            ...mapMutations([
                'updateToken'
            ]),
            handleSubmit2 (ev) {
                this.$refs.ruleForm2.validate((valid) => {
                    if (valid) {
                        this.logining = true
                        var loginParams = {user: this.ruleForm2.account, password: this.ruleForm2.checkPass}
                        requestLogin(loginParams).then(data => {
                            this.logining = false
                            let {msg, code} = data
                            let token = data.data.token
                            if (code !== 200) {
                                this.$message({
                                    message: msg,
                                    type: 'error'
                                })
                            } else {
                                //更新token
                                this.updateToken(token)
                                this.$router.push({path: '/deviceBoardA'})
                            }
                        })
                    } else {
                        console.log('error submit!!')
                        return false
                    }
                })
            }
        }
    }

</script>

<style lang="scss" scoped>
    .login {
        width: 990px;
        margin: 0 auto;
        .login-header {
            display: flex;
            align-items: center;
            h1 {
                font-size: 48px;
                color: #0099FF;
                margin-left: 50px;
            }
        }
        .bg {
            position: relative;
            width: 100%;
            height: 565px;
            background: url("../assets/imgs/login-bg.jpg") no-repeat;
            background-size: cover;
            .form {
                position: absolute;
                top: 155px;
                right: 20px;
                border-radius: 5px;
                box-sizing: border-box;
                width: 280px;
                padding: 20px;
                background: rgba(255,255,255,0.737);
                border: 1px solid #eaeaea;
                box-shadow: 0 0 25px #cac6c6;
                .title {
                    font-size: 20px;
                    font-weight: bold;
                    margin: 0px auto 40px auto;
                    text-align: center;
                    color: #505458;
                }
            }
        }
        .copyright{
            height:65px;
            line-height: 65px;
            font-size: 16px;
            width: 100%;
            text-align: center;
        }
    }
</style>


