<template>
    <!--<el-col :span="24" class="header">
        <el-col :span="10" class="logo logo-width">
            <span>盐田国际海关电子闸监控系统</span>
        </el-col>
        <el-col :span="4" class="userinfo">
            <el-dropdown trigger="click">
                <span class="user-name">{{userName}}</span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native = "handleChange">修改密码</el-dropdown-item>
                    <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </el-col>
        &lt;!&ndash;更改密码&ndash;&gt;
        <el-dialog title="更改密码" v-model="changeFormVisible" :close-on-click-modal="false" size="tiny">
            <el-form :model="changeForm" label-width="80px"  ref="changeForm" :rules="changeFormRules">
                <el-form-item label="当前密码" prop="name">
                    <el-input v-model="changeForm.old_passwd" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="新密码">
                    <el-input v-model="changeForm.new_passwd" auto-complete="off"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="changeFormVisible = false">取消</el-button>
                <el-button type="primary" @click="changeSubmit" :loading="loading">提交</el-button>
            </div>
        </el-dialog>
    </el-col>-->
    <ul class="el-menu el-menu-vertical-demo collapsed" v-show="collapsed" ref="menuCollapsed">
        <li v-for="(item,index) in $router.options.routes" v-if="!item.hidden" class="el-submenu item">
            <template v-if="!item.leaf">
                <div class="el-submenu__title" style="padding-left: 20px;" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"><i :class="item.iconCls"></i></div>
                <ul class="el-menu submenu" :class="'submenu-hook-'+index" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)">
                    <li v-for="child in item.children" v-if="!child.hidden" :key="child.path" class="el-menu-item" style="padding-left: 40px;" :class="$route.path==child.path?'is-active':''" @click="$router.push(child.path)">{{child.name}}</li>
                </ul>
            </template>
            <template v-else>
                <li class="el-submenu">
                    <div class="el-submenu__title el-menu-item" style="padding-left: 20px;height: 56px;line-height: 56px;padding: 0 20px;" :class="$route.path==item.children[0].path?'is-active':''" @click="$router.push(item.children[0].path)"><i :class="item.iconCls"></i></div>
                </li>
            </template>
        </li>
    </ul>

</template>

<script>
    import {mapActions, mapState} from "vuex"
    import api from "@/api/index"
    export default {
        data() {
            return {
                token: localStorage.getItem("token"),
                loading:false,
                changeFormVisible:false,
                changeForm: {
                    old_passwd: "",
                    new_passwd: ""
                },
                changeLoading:false,
                changeFormRules: {
                    old_passwd: [
                        {required: true, message: '请输入当前密码', trigger: 'blur'}
                    ],
                    new_passwd: [
                        {required: true, message: '请输入新密码', trigger: 'blur'}
                    ],
                },
            }
        },
        computed: {
        },
        props:{
            userName:{
                required:true
            }
        },
        methods: {
            //退出登录
            logout() {
                this.$confirm('确认退出吗?', '提示', {
                    //type: 'warning'
                }).then(() => {
                    localStorage.removeItem('token');
                    this.$router.push('/login');
                }).catch(() => {
                });
            },
            //显示新增界面
            handleChange() {
                this.changeFormVisible = true
                this.changeForm = {
                    user: '',
                    password: ''
                }
            },
            //新增
            changeSubmit (){
                this.$refs.changeForm.validate((valid) => {
                    if (valid) {
                        this.loading = true
                        let para = Object.assign({}, this.changeForm,{token:this.token})
                        api.changePwd(para).then((res) => {
                            this.loading = false
                            this.$message({
                                message: '更改成功',
                                type: 'success'
                            })
                            this.$refs['changeForm'].resetFields()
                            this.changeFormVisible = false
                        })
                    }
                })
            }

        },
        mounted() {
        },
        created(){
        }
    }

</script>


<style lang="scss" scoped="">
    @import '~scss_vars';
    .header {
        height: 60px;
        line-height: 60px;
        background: $color-primary;
        color: #fff;
        .userinfo {
            text-align: right;
            padding-right: 35px;
            float: right;
            .user-name {
                cursor: pointer;
                display: inline-block;
                font-size: 16px;
                color: #fff;
                height: 30px;
            }
        }
        .logo {
            //width:230px;
            height: 60px;
            font-size: 16px;
            padding-left: 5px;
            padding-right: 5px;
            border-color: rgba(238, 241, 146, 0.3);
            border-right-width: 1px;
            border-right-style: solid;
            img {
                width: 40px;
                float: left;
                margin: 10px 10px 10px 18px;
            }
            .txt {
                color: #fff;
            }
        }
        .logo-width {
            width: 230px;
        }
        .tools {
            padding: 0px 23px;
            width: 14px;
            height: 60px;
            line-height: 60px;
            cursor: pointer;
        }
    }
</style>


