import Vue from 'vue'
import Router from 'vue-router'
/*import NProgress from 'nprogress'
import 'nprogress/nprogress.css'*/

import Login from './views/Login.vue'
import Home from './views/Home.vue'
import NotFound from './views/404.vue'

const deviceBoardA = resolve => require(['./views/status/device-board-a.vue'], resolve)
const deviceBoardB = resolve => require(['./views/status/device-board-b.vue'], resolve)

const recordA = resolve => require(['./views/record/record-a.vue'], resolve)
const recordB = resolve => require(['./views/record/record-b.vue'], resolve)

const controlA = resolve => require(['./views/control/control-a.vue'], resolve)
const controlB = resolve => require(['./views/control/control-b.vue'], resolve)

const recordSearch = resolve => require(['./views/search/record-search.vue'], resolve)
const operationSearch = resolve => require(['./views/search/operation-search.vue'], resolve)
const outInA = resolve => require(['./views/search/out-in-statistics-a.vue'], resolve)
const outInB = resolve => require(['./views/search/out-in-statistics-b.vue'], resolve)

const admin = resolve => require(['./views/user-admin/admin.vue'], resolve)

Vue.use(Router)

let routes = [
    {
        path: '/',
        redirect: '/deviceBoardA',
        hidden: true,
    },
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '设备状态',
        iconCls: 'el-icon-message',//图标样式class
        children: [
            { path: '/deviceBoardA', component: deviceBoardA, name: 'A出闸状态' },
            { path: '/deviceBoardB', component: deviceBoardB, name: 'B出闸状态' },
        ],
        requireAdmin: false
    },
    {
        path: '/',
        component: Home,
        name: '读单数据',
        iconCls: 'el-icon-document',
        children: [
            { path: '/recordA', component: recordA, name: 'A出闸读单' },
            { path: '/recordB', component: recordB, name: 'B出闸读单' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '远程控制',
        iconCls: 'el-icon-setting',
        children: [
            { path: '/controlA', component: controlA, name: 'A出闸控制' },
            { path: '/controlB', component: controlB, name: 'B出闸控制' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '查询统计',
        iconCls: 'el-icon-search',
        children: [
            { path: '/recordSearch', component: recordSearch, name: '读单数据查询' },
            { path: '/outInA', component: outInA, name: 'A进出统计' },
            { path: '/outInB', component: outInB, name: 'B进出统计' },
            { path: '/operationSearch', component: operationSearch, name: '操作记录' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '后台管理',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/admin', component: admin, name: '管理员操作' },
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];


const router = new Router({
    routes
})

//NProgress.configure({ showSpinner: false });

router.beforeEach((to, from, next) => {
    // NProgress.start();

    // user判断的控制路由，耦合了login等
    if (to.path == '/login') {
        localStorage.removeItem('token');
    }

    let token = localStorage.getItem('token');
    if (!token && to.path != '/login') {
        next({
            path: '/login',
            query: {redirect: to.fullPath}
        })
    } else {
        next()
    }
})

/*router.afterEach(transition => {
    NProgress.done();
});*/

export default router;


