<template>
    <el-row class="container">
        <el-col :span="24" class="header">
            <el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
                <span>盐田国际海关电子闸监控系统</span>
            </el-col>
            <el-col :span="10">
                <div class="tools" @click.prevent="collapse">
                    <i class="el-icon-d-arrow-left" v-show="!collapsed"></i>
                    <i class="el-icon-d-arrow-right" v-show="collapsed"></i>
                </div>
            </el-col>
            <el-col :span="4" class="userinfo">
                <el-dropdown trigger="click">
                    <span class="user-name">{{userName}}</span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native = "handleChange">修改密码</el-dropdown-item>
                        <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </el-col>
            <!--更改密码-->
            <el-dialog title="更改密码" v-model="changeFormVisible" :close-on-click-modal="false" size="tiny">
                <el-form :model="changeForm" label-width="80px"  ref="changeForm" :rules="changeFormRules">
                    <el-form-item label="当前密码" prop="name">
                        <el-input v-model="changeForm.old_passwd" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="新密码">
                        <el-input v-model="changeForm.new_passwd" auto-complete="off"></el-input>
                    </el-form-item>

                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="changeFormVisible = false">取消</el-button>
                    <el-button type="primary" @click="changeSubmit" :loading="loading">提交</el-button>
                </div>
            </el-dialog>
        </el-col>
        <el-col :span="24" class="main">
            <aside :class="collapsed ? 'menu-collapsed':'menu-expanded'">
                <!--导航菜单-->
                <el-menu :default-active="$route.path" class="el-menu-vertical-demo"
                         unique-opened router v-if="!collapsed">
                    <template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
                        <el-submenu :index="index+''" v-if="!item.leaf">
                            <template slot="title"><i :class="item.iconCls"></i>{{item.name}}</template>
                            <el-menu-item v-for="child in item.children" :index="child.path" :key="child.path" v-if="!child.hidden">{{child.name}}</el-menu-item>
                        </el-submenu>
                        <el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path"><i :class="item.iconCls"></i>{{item.children[0].name}}</el-menu-item>
                    </template>
                </el-menu>
                <!--导航菜单-折叠后-->
                <ul class="el-menu el-menu-vertical-demo collapsed" v-if="collapsed" ref="menuCollapsed">
                    <li v-for="(item,index) in $router.options.routes" v-if="!item.hidden" class="el-submenu item">
                        <template v-if="!item.leaf">
                            <div class="el-submenu__title" style="padding-left: 20px;" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"><i :class="item.iconCls"></i></div>
                            <ul class="el-menu submenu" :class="'submenu-hook-'+index" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)">
                                <li v-for="child in item.children" v-if="!child.hidden" :key="child.path" class="el-menu-item" style="padding-left: 40px;" :class="$route.path==child.path?'is-active':''" @click="$router.push(child.path)">{{child.name}}</li>
                            </ul>
                        </template>
                        <template v-else>
                            <li class="el-submenu">
                                <div class="el-submenu__title el-menu-item" style="padding-left: 20px;height: 56px;line-height: 56px;padding: 0 20px;" :class="$route.path==item.children[0].path?'is-active':''" @click="$router.push(item.children[0].path)"><i :class="item.iconCls"></i></div>
                            </li>
                        </template>
                    </li>
                </ul>
            </aside>
            <section class="content-container">
                <div class="grid-content bg-purple-light">
                    <el-col :span="24" class="breadcrumb-container">
                        <strong class="title">{{$route.name}}</strong>
                        <el-breadcrumb separator="/" class="breadcrumb-inner">
                            <el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
                                {{ item.name }}
                            </el-breadcrumb-item>
                        </el-breadcrumb>
                    </el-col>
                    <el-col :span="24" class="content-wrapper">
                        <transition name="fade" mode="out-in">
                            <router-view></router-view>
                        </transition>
                    </el-col>
                </div>
            </section>
        </el-col>
    </el-row>
</template>

<script>
    import appHeader from "@/components/header.vue"
    import api from '@/api/index'
    export default {
        data() {
            return {
                collapsed:false,
                userName:"",
                isAdmin:false,
                loading:false,
                changeFormVisible:false,
                changeForm: {
                    old_passwd: "",
                    new_passwd: ""
                },
                changeLoading:false,
                changeFormRules: {
                    old_passwd: [
                        {required: true, message: '请输入当前密码', trigger: 'blur'}
                    ],
                    new_passwd: [
                        {required: true, message: '请输入新密码', trigger: 'blur'}
                    ],
                },
            }
        },
        computed:{
            filterRouters(){
                if(this.isAdmin){
                    return this.$router.options.routes
                }
                else {
                    return this.$router.options.routes.filter((route) =>{
                       return route.requireAdmin == false
                    })
                }
            }
        },
        methods: {
            //折叠导航栏
            collapse (){
                this.collapsed=!this.collapsed;
            },
            showMenu (i,status){
                this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-'+i)[0].style.display=status?'block':'none';
            },
            //退出登录
            logout() {
                this.$confirm('确认退出吗?', '提示', {
                    //type: 'warning'
                }).then(() => {
                    localStorage.removeItem('token');
                    this.$router.push('/login');
                }).catch(() => {
                });
            },
            //显示新增界面
            handleChange() {
                this.changeFormVisible = true
                this.changeForm = {
                    user: '',
                    password: ''
                }
            },
            //新增
            changeSubmit (){
                this.$refs.changeForm.validate((valid) => {
                    if (valid) {
                        this.loading = true
                        let para = Object.assign({}, this.changeForm,{token:this.token})
                        api.changePwd(para).then((res) => {
                            this.loading = false
                            this.$message({
                                message: '更改成功',
                                type: 'success'
                            })
                            this.$refs['changeForm'].resetFields()
                            this.changeFormVisible = false
                        })
                    }
                })
            }
        },
        components:{appHeader},
        created(){
            api.getBaseInfo({
                token: localStorage.getItem('token')
            }).then((res) =>{
//                 console.log(res)
                 this.userName = res.data.userName
                 this.isAdmin = res.data.isAdmin
             })

        }
    }

</script>

<style scoped lang="scss">
    @import '~scss_vars';

    .container {
        position: absolute;
        top: 0px;
        bottom: 0px;
        width: 100%;
        .header {
            height: 60px;
            line-height: 60px;
            background: $color-primary;
            color: #fff;
            .userinfo {
                text-align: right;
                padding-right: 35px;
                float: right;
                .user-name {
                    cursor: pointer;
                    display: inline-block;
                    font-size: 16px;
                    color: #fff;
                    height: 30px;
                }
            }
            .logo {
                //width:230px;
                height: 60px;
                font-size: 16px;
                padding-left: 5px;
                padding-right: 5px;
                span{
                    display: inline-block;
                    height: 60px;
                }
            }
            .logo-width {
                width: 230px;
            }
            .logo-collapse-width{
                width:50px
            }
            .tools{
                padding: 0px 23px;
                width:14px;
                height: 60px;
                line-height: 60px;
                cursor: pointer;
            }
        }
        .main {
            display: flex;
            position: absolute;
            top: 60px;
            bottom: 0px;
            overflow: hidden;
            aside {
                flex:0 0 230px;
                width: 230px;
                .el-menu {
                    height: 100%;
                }
                .collapsed {
                    width: 60px;
                    .item {
                        position: relative;
                    }
                    .submenu {
                        position: absolute;
                        top: 0px;
                        left: 60px;
                        z-index: 99999;
                        height: auto;
                        display: none;
                    }

                }
                &.menu-collapsed {
                    flex: 0 0 60px;
                    width: 60px;
                }
                &.menu-expanded {
                    flex: 0 0 230px;
                    width: 230px;
                }
            }

            .content-container {
                flex: 1;
                overflow-y: scroll;
                padding: 10px;
                /*padding: 20px;*/
                .breadcrumb-container {
                    display: flex;
                    justify-content: space-between;
                    .title {
                        width: 200px;
                        /*float: left;*/
                        color: #475669;
                    }
                    .breadcrumb-inner {
                        /*float: right;*/
                    }
                }
                .content-wrapper {
                    background-color: #fff;
                    box-sizing: border-box;
                }
            }
        }
    }
</style>


