import axios from 'axios'

let baseUrl = process.env.NODE_ENV =='development' ? 'http://115.28.246.26:8001/' : 'http://115.28.246.26:8001/' ;

//注意 /斜线

// 两种不同的export写法
export { baseUrl }

// 用户管理
export const requestLogin = params => {
    return axios.post(`${baseUrl}account/user-login/ `, params).then(res => res.data)
}

export const getUserList = params => {
    return axios.get(`${baseUrl}account/user-list/`, {params: params})
}

export const addUser = params => {
    return axios.post(`${baseUrl}account/create-new-user/`, params)
}

export const resetPwd = params => {
    return axios.post(`${baseUrl}account/reset-password/`, params).then(res => res.data)
}

export const deletUser = params => {
    return axios.get(`${baseUrl}account/del-user/`, {params: params}).then(res => res.data)
}

export const getBaseInfo = params => {
    return axios.get(`${baseUrl}account/base-info/`, {params: params}).then(res => res.data)
}

export const changePwd = params => {
    return axios.post(`${baseUrl}account/change-password/`, params).then(res => res.data)
}

export const addAdmin = params => {
    return axios.post(`${baseUrl}account/add-admin/`, params).then(res => res.data)
}

// 设备状态 分a/b
export const getDeviceInfo = params => {
    return axios.get(`${baseUrl}monitor/device-info/?time=${+ new Date()}`, {params: params}).then(res => res.data)
}

//读单数据获取(全部)（普通用户）
export const getIoRecord = params => {
    return axios.get(`${baseUrl}monitor/io-record/`, {params: params}).then(res => res.data)
}

//读单数据分gate（普通用户）
export const getGateRecord = params => {
    return axios.get(`${baseUrl}monitor/gate-record/`, {params: params}).then(res => res.data)
}

//读单数据查询（管理员）
export const searchIoRecord = params => {
    return axios.get(`${baseUrl}monitor/get-record/`, {params: params}).then(res => res.data)
}

export const controlDevice = params => {
    return axios.post(`${baseUrl}monitor/control-device/`, params).then(res => res.data)
}

export const getOperationRecord = params => {
    return axios.get(`${baseUrl}monitor/history-list/`, {params: params}).then(res => res.data)
}

export const downData = params => {
    return axios.get(`${baseUrl}monitor/down-data/`, {params: params}).then(res => res.data)
}


// 进出统计

export const getCount = params => {
    return axios.get(`${baseUrl}monitor/get-count/`, {params: params}).then(res => res.data)
}









